import React, { Component } from 'react'
import styled from 'styled-components'
import * as R from 'ramda'
import { Button, Row, Col, Modal, BackTop } from 'antd'
import 'codemirror/lib/codemirror.css'
import 'codemirror/mode/javascript/javascript'
import { Controlled as CodeMirror } from 'react-codemirror2'

import {
  adjustRadius,
  codeToProfile,
  defaultLocation,
  getIterators,
  getLocationFromCoordinates
} from './utils'
import './App.css'
import ResultsTable from './ResultsTable'
import FixtureSelector from './FixtureSelector'
import RadiusSelector from './RadiusSelector'
import Network from './Network'
import { state as fetchState, reset as fetchReset } from './fetch'
import Map from './Map'
import stringifyObject from './vendor/stringify-object'
import fixtures from './fixtures'

const exampleProfile = `
{
  category: 'auto',
  keyword: 'BMW',
  radius: { min: 0, max: 25 }
}
`

const Container = styled.div``

const TypeErrorMessage = styled.div`color: red;`

const formatProfileObject = obj =>
  stringifyObject(obj, { indent: '  ', inlineCharacterLimit: 42 })

class App extends Component {
  state = {
    code:
      window.localStorage.getItem('FINDY_SOURCES_PROFILE') || exampleProfile,
    iterators: [],
    radius: { min: 0, max: 25 },
    geolocation: defaultLocation
  }

  updateCode = newCode => {
    window.localStorage.setItem('FINDY_SOURCES_PROFILE', newCode)
    this.setState({
      code: newCode
    })
    const profile = codeToProfile(newCode)
    if (profile) {
      try {
        const iterators = getIterators(
          this.props.sources,
          newCode,
          this.state.geolocation,
          this.state.radius
        )
        this.setState(({ radius }) => ({
          syntaxError: false,
          typeError: undefined,
          iterators,
          latestIterator: undefined,
          nextContext: undefined,
          usedContext: undefined,
          category: profile.category,
          map: {
            center: profile.geolocation
          },
          radius: adjustRadius(profile, radius)
        }))
      } catch (e) {
        this.setState({ typeError: e.message })
      }
    } else {
      this.setState({ syntaxError: true })
    }
  }

  resetIteration = () => {
    const iterators = getIterators(
      this.props.sources,
      this.state.code,
      this.state.geolocation,
      this.state.radius
    )
    this.setState({
      iterators,
      latestIterator: undefined,
      nextContext: undefined,
      usedContext: undefined
    })
  }

  updateRadius = radius => {
    this.setState({ radius }, this.resetIteration)
  }

  onCenterChange = async latlng => {
    this.setState(
      { geolocation: await getLocationFromCoordinates(latlng) },
      this.resetIteration
    )
  }

  formatProfile = () => {
    const obj = codeToProfile(this.state.code)
    if (obj) {
      this.updateCode(formatProfileObject(obj))
    }
  }

  debugMapping = () => {
    const profile = codeToProfile(this.state.code)
    if (profile) {
      this.props.sources.setOptions({ debugMapping: true })
      this.props.sources.run({
        geolocation: this.state.geolocation,
        ...profile
      })({ radius: profile.radius || this.state.radius })
      this.props.sources.setOptions({ debugMapping: false })
    }
  }

  useFixture = fixtureId => {
    const fixture = R.find(R.propEq('fixtureId', fixtureId), fixtures).profile
    const profile = R.dissoc('geolocation', fixture)
    this.updateCode(formatProfileObject(profile))
  }

  run = async (iterator, debug, context = null) => {
    this.setState({
      result: undefined,
      values: [],
      calls: [],
      loading: iterator.name,
      loadingMore: Boolean(context)
    })
    fetchReset()

    // if there is no context - reinstantiate the iterator
    if (!context) {
      iterator = getIterators(
        this.props.sources,
        this.state.code,
        this.state.geolocation,
        this.state.radius
      ).find(i => i.name === iterator.name)
    }

    if (debug) {
      debugger
    }
    const promise = iterator.iterate(context)
    try {
      const data = await promise
      window.$values = data.values
      window.$discards = data.discards
      window.$result = data

      this.setState({
        values: data.values, // deprecated
        result: data,
        nextContext: data.context,
        usedContext: context,
        latestIterator: iterator,
        calls: fetchState.calls,
        loading: undefined,
        loadingMore: undefined
      })
    } catch (e) {
      Modal.error({
        title: 'Error in sourcing code occured',
        content: <pre style={{ fontSize: 11 }}>{e.stack}</pre>,
        width: 1000
      })
      this.setState({
        values: [],
        nextContext: undefined,
        usedContext: context,
        latestIterator: iterator,
        calls: fetchState.calls,
        loading: undefined,
        loadingMore: undefined
      })
    }
  }

  componentDidMount() {
    this.updateCode(this.state.code)
  }

  render() {
    return (
      <Container>
        <Row gutter={16}>
          <Col span={8}>
            <FixtureSelector
              style={{ width: '100%' }}
              onChange={this.useFixture}
            />
          </Col>
          <Col span={6}>
            <Button
              disabled={this.state.syntaxError}
              onClick={this.debugMapping}
            >
              Debug mapping to the console
            </Button>
          </Col>
          <Col span={8}>
            <RadiusSelector
              radius={this.state.radius}
              category={this.state.category}
              onChange={this.updateRadius}
            />
          </Col>
        </Row>
        <Row gutter={16} style={{ marginTop: 16 }}>
          <Col span={8}>
            <CodeMirror
              value={this.state.code}
              onBeforeChange={(editor, data, value) => this.updateCode(value)}
              onBlur={this.formatProfile}
              options={{ mode: 'javascript' }}
              className={this.state.syntaxError && 'code-error'}
            />
            {this.state.typeError && (
              <TypeErrorMessage>{this.state.typeError}</TypeErrorMessage>
            )}
          </Col>
          <Col span={6}>
            {this.state.iterators.map(iterator => (
              <Button
                style={{
                  marginRight: 8,
                  marginBottom: 8
                }}
                key={iterator.name}
                onClick={e => this.run(iterator, e.metaKey)}
                loading={
                  this.state.loading === iterator.name &&
                  !this.state.loadingMore
                }
                disabled={this.state.loading}
              >
                {iterator.name}
              </Button>
            ))}
            <Row gutter={8}>
              <Col span={12}>
                {this.state.usedContext && (
                  <React.Fragment>
                    <h3>Used Context</h3>
                    <CodeMirror
                      value={JSON.stringify(this.state.usedContext, null, 2)}
                      options={{ mode: 'javascript', readonly: true }}
                    />
                  </React.Fragment>
                )}
              </Col>
              <Col span={12}>
                {this.state.nextContext && (
                  <React.Fragment>
                    <h3>Next Context</h3>
                    <CodeMirror
                      value={JSON.stringify(this.state.nextContext, null, 2)}
                      options={{ mode: 'javascript', readonly: true }}
                      // onChange={(editor, data, code) => this.setState({ context: JSON.parse(code) })}
                    />
                    <Button
                      style={{ marginTop: 8, marginBottom: 8 }}
                      onClick={e =>
                        this.run(
                          this.state.latestIterator,
                          e.metaKey,
                          this.state.nextContext
                        )}
                      disabled={
                        this.state.loading || this.state.nextContext.done
                      }
                      loading={this.state.loadingMore}
                    >
                      more from {this.state.latestIterator.name}
                    </Button>
                  </React.Fragment>
                )}
              </Col>
            </Row>
          </Col>
          <Col span={10}>
            {this.state.map && (
              <Map
                result={this.state.result}
                center={this.state.geolocation}
                radius={this.state.radius}
                onCenterChange={this.onCenterChange}
              />
            )}
          </Col>
        </Row>

        {/*<CLMap/>*/}
        <Network calls={this.state.calls} />
        {this.state.result && <ResultsTable result={this.state.result} />}
        <BackTop />
      </Container>
    )
  }
}

export default App
