import { radiusStepsForProfile } from '@findy-sources/meta'
import * as R from 'ramda'

/**
 * Returns current radius if it's correct, otherwise returns smallest correct radius
 */
export function adjustRadius(profile, radius) {
  const steps = radiusStepsForProfile(profile)
  const isValid = R.contains(radius.min, steps) && R.contains(radius.max, steps)
  return isValid ? radius : { min: steps[0], max: steps[1] }
}

/**
 * Converts textual profile code to the profile object
 */
export function codeToProfile(code) {
  try {
    // eslint-disable-next-line
    return eval(`(${code})`)
  } catch (e) {
    return undefined
  }
}

export const defaultLocation = {
  lat: 33.67605162321932,
  lng: -117.63531161483156,
  zip: '92679',
  country: 'US',
  state: 'CA',
  city: 'Trabuco Canyon',
  short: 'US, CA, Trabuco Canyon',
  full: '28392 Millwood Rd, Trabuco Canyon, CA 92679, USA'
}

/**
 * Code to profile with geolocation
 *
 * @deprecated
 */
export function processProfile(code) {
  const obj = codeToProfile(code)
  if (obj) {
    const { geolocation = defaultLocation, ...profile } = obj
    return { ...profile, geolocation }
  }
}

export function getIterators(sources, code, geolocation, radius) {
  const profile = codeToProfile(code)
  if (!profile) return []
  return sources.run({ geolocation, ...profile })({
    radius: profile.radius || radius
  })
}

const getFirstAddress = R.pipe(
  R.sortBy(x => -x.address_components.length),
  R.head
)

const getComponent = (name, long = false) => components => {
  const component = R.find(({ types }) => R.contains(name, types), components)
  if (component) {
    return long ? component.long_name : component.short_name
  }
}

function extractLocation(results) {
  const firstAddress = getFirstAddress(results)
  if (!firstAddress) {
    throw 'Unable to reverse geocode the location'
  }
  const {
    formatted_address,
    address_components,
    geometry: { location }
  } = firstAddress

  const address = {
    state: getComponent('administrative_area_level_1')(address_components),
    zip: getComponent('postal_code', true)(address_components),
    country: getComponent('country')(address_components),
    neighborhood: getComponent('neighborhood', true)(address_components),
    city: getComponent('locality', true)(address_components),
    full: formatted_address,
    lat: location.lat(),
    lng: location.lng()
  }

  address['short'] = R.pipe(
    R.props(
      address.neighborhood
        ? ['country', 'city', 'neighborhood']
        : ['country', 'state', 'city']
    ),
    R.filter(R.identity),
    R.join(', ')
  )(address)
  return address
}

function geocode(options) {
  const geocoder = new window.google.maps.Geocoder()
  return new Promise(resolve =>
    geocoder.geocode(options, (results, status) => resolve({ results, status }))
  )
}

export async function getLocationFromCoordinates(location) {
  const { results, status } = await geocode({ location })

  if (status !== 'OK') {
    throw 'Unable to reverse geocode the location'
  }

  return extractLocation(results)
}
