import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'

import { createRequest} from "@findy-sources/request"
import App from './App'
import Mapping from './mapping/Mapping'
import fetchHttpFn from './fetch'

window.request = createRequest(fetchHttpFn, true)

class Routing extends Component {
  state = {}
  componentDidMount() {
    this.updateSources()
  }
  updateSources = async () => {
    const URL = process.env.PUBLIC_URL + '/sources.js'
    const code = await (await fetch(URL)).text()
    const { Sources } = new Function(
      `const exports = {};\n${code}\nreturn exports`
    )()
    const sources = Sources({
      httpFetchFn: fetchHttpFn,
      enableHilton: true,
      enableUnits: true,
      enableChunking: false,
      enableDelivery: true,
      endpointApi: 'https://sources.findy.com'
    })
    this.setState({ sources })
    console.log('sources updated')
  }
  render() {
    return (
      <Router>
        <div>
          <div style={{ textAlign: 'center' }}>
            <a href="" onClick={this.updateSources}>
              Update Source Lib
            </a>{' '}
            {/*| <Link to="/">Home</Link> | <Link to="/mapping">Mapping</Link>*/}
          </div>
          {this.state.sources && (
            <React.Fragment>
              <Route
                exact
                path="/"
                render={() => <App sources={this.state.sources} />}
              />
              {/*<Route*/}
                {/*path="/mapping/:categoryId?/:filterId?"*/}
                {/*render={() => <Mapping sources={this.state.sources} />}*/}
              {/*/>*/}
            </React.Fragment>
          )}
        </div>
      </Router>
    )
  }
}

export default Routing
