import { default as React, Component } from 'react'

import { withGoogleMap, GoogleMap, Marker, Circle } from 'react-google-maps'

const Radius = props => (
  <Circle
    center={props.center}
    radius={props.radius * 1609.34}
    onClick={props.onClick}
    options={{
      fillOpacity: 0.08,
      strokeColor: props.color,
      strokeOpacity: 1,
      strokeWeight: 1
    }}
  />
)

const ItemMarker = ({ type, item }) => {
  const { location: { lat, lon } = {} } = item
  return lat && lon ? (
    <Marker
      icon={{
        scaledSize: { width: 10, height: 15 },
        url: type === 'discard' ? '/marker-discard.png' : '/marker-value.png'
      }}
      position={{ lat, lng: lon }}
      defaultAnimation={0}
    />
  ) : null
}

const MapComponent = withGoogleMap(props => (
  <GoogleMap
    defaultZoom={8}
    center={props.center}
    onClick={e =>
      props.onCenterChange({ lat: e.latLng.lat(), lng: e.latLng.lng() })}
  >
    {props.center && (
      <div>
        {props.radius.min !== undefined && (
          <Radius
            center={props.center}
            radius={props.radius.min}
            color="green"
          />
        )}
        {props.radius.max !== undefined && (
          <Radius
            onClick={e =>
              props.onCenterChange({
                lat: e.latLng.lat(),
                lng: e.latLng.lng()
              })}
            center={props.center}
            radius={props.radius.max}
            color="red"
          />
        )}
      </div>
    )}
    <Marker
      icon={{
        url: '/marker-center.png',
        scaledSize: { width: 10, height: 15 }
      }}
      position={props.center}
      defaultAnimation={0}
    />
    {props.result &&
      props.result.values &&
      props.result.values.map(value => (
        <ItemMarker key={value.id} item={value} type="value" />
      ))}
    {props.result &&
      props.result.discards &&
      props.result.discards.map(value => (
        <ItemMarker key={value.item.id} item={value.item} type="discard" />
      ))}
  </GoogleMap>
))

export default class ToolMapComponent extends Component {
  constructor(props) {
    super()
  }

  render() {
    return (
      <MapComponent
        containerElement={<div style={{ height: `300px` }} />}
        mapElement={<div style={{ height: `300px` }} />}
        center={this.props.center}
        radius={this.props.radius}
        result={this.props.result}
        onCenterChange={this.props.onCenterChange}
      />
    )
  }
}
