import React, { Component } from 'react'
import styled from 'styled-components'
import * as R from 'ramda'
import { Link } from 'react-router-dom'

const Element = styled.div`
  font-size: 13px;
  margin-left: 1em;
`

const Category = props => {
  return (
    <Element>
      {props.name}
      <div>
        {Object.entries(props.filters).map(([filterName, filter]) => (
          <Element key={filterName}>
            <Link to={`/mapping/${props.name}/${filterName}`}>
              {filterName}
            </Link>
          </Element>
        ))}
      </div>
    </Element>
  )
}

const selectFilters = R.pipe(
  R.map(R.pickBy(x => x.type === 'list' || x.type === 'compound')),
  R.pickBy(R.complement(R.isEmpty))
)

class SideBar extends Component {
  render() {
    const filters = selectFilters(this.props.filters)
    return (
      <div>
        {Object.entries(filters).map(([name, filters]) => (
          <Category
            key={name}
            name={name}
            match={this.props.match}
            filters={filters}
          />
        ))}
      </div>
    )
  }
}

export default SideBar
