import React from 'react'
import Waypoint from 'react-waypoint'
import { Table, Popover, Tag, Tooltip, Tabs } from 'antd'
import Inspector, { ObjectRootLabel, ObjectLabel } from 'react-inspector'
import { pure } from 'recompose'
import { Value } from 'react-powerplug'
import styled from 'styled-components'

const rawTooltipNodeRenderer = ({ depth, name, data, isNonenumerable }) => (
  <ObjectLabel name={name} data={data} isNonenumerable={isNonenumerable} />
)

const Ellipsis = styled.span`
  width: ${props => props.width}px;
  text-overflow: ellipsis;
  display: inline-block;
  overflow: hidden;
`

const formatCell = val => {
  if (
    val !== null &&
    typeof val === 'object' &&
    (val.min !== undefined || val.max !== undefined)
  ) {
    if (val.min === val.max) {
      return `${val.min}`
    }
    return `${val.min}  ${val.max}`
  } else if (typeof val === 'string' || typeof val === 'number') {
    return val
  } else if (Array.isArray(val)) {
    return val.map(x => <Tag key={x}>{x}</Tag>)
  }

  return JSON.stringify(val)
}

const mainColumns = [
  { title: 'id', dataIndex: 'id', width: '50px' },
  {
    key: 'raw',
    title: 'raw',
    width: '50px',
    render: v => (
      <Popover
        style={{ width: 400 }}
        content={
          <Inspector
            data={v}
            expandLevel={5}
            nodeRenderer={rawTooltipNodeRenderer}
          />
        }
      >
        raw
      </Popover>
    )
  },
  {
    title: 'photoUrl',
    dataIndex: 'photoUrl',
    width: '100px',
    render: v => (
      <Value initial={false}>
        {({ set, value }) => (
          <React.Fragment>
            <Waypoint scrollableAncestor="window" onEnter={() => set(true)} />
            {value && (
              <Popover content={<img src={v} />} placement="right">
                <img src={v} style={{ height: 50 }} />
              </Popover>
            )}
          </React.Fragment>
        )}
      </Value>
    )
  },
  {
    title: 'title',
    dataIndex: 'title',
    width: '250px',
    render: v => (
      <Tooltip title={v}>
        <Ellipsis width={250}>{v}</Ellipsis>
      </Tooltip>
    )
  },
  { title: 'url', dataIndex: 'url', render: v => <a href={v}>link</a> },
  { title: 'category', dataIndex: 'category', width: '50px' }
]

const buildTableColumns = values => {
  const item = values[0]
  if (!item) return []
  const { attributes, location, fails } = item

  const attributeColumns = Object.keys(attributes || {})
    .sort()
    .map(key => ({
      title: key,
      dataIndex: `attributes.${key}`,
      render: v => <span>{formatCell(v)}</span>
    }))

  const locationColumns = Object.keys(location || {})
    .sort()
    .map(key => ({
      title: key,
      dataIndex: `location.${key}`
    }))
  return [
    ...(fails
      ? [
          {
            title: 'fails',
            dataIndex: 'fails',
            render: x =>
              x.map(fail => <div key={fail.reason}>{fail.reason}</div>)
          }
        ]
      : []),
    ...mainColumns,
    ...(attributes
      ? [{ title: 'attributes', children: attributeColumns }]
      : []),
    ...(location ? [{ title: 'location', children: locationColumns }] : [])
  ]
}

const ItemTable = ({ data }) => (
  <Table
    dataSource={data}
    columns={buildTableColumns(data)}
    scroll={{ x: true }}
    style={{ whiteSpace: 'nowrap' }}
    rowKey={'id'}
    pagination={false}
    size="small"
  />
)

export default pure(({ result: { values, discards } }) => (
  <Tabs>
    <Tabs.TabPane key="values" tab={`Good items (${values.length})`}>
      <ItemTable data={values} />
    </Tabs.TabPane>
    <Tabs.TabPane key="discards" tab={`Discards (${discards.length})`}>
      <ItemTable
        data={discards.map(({ fails, item }) => ({ fails, ...item }))}
      />
    </Tabs.TabPane>
  </Tabs>
))
