import { fixtureHelpers, fixtures } from '@findy-sources/fixtures'
import * as padStart from 'string.prototype.padstart'

const assignFixtureIds = fixtures => {
  const counter = {}
  return fixtures.map(profile => {
    counter[profile.category] = (counter[profile.category] || 0) + 1
    return {
      profile,
      fixtureId: [
        profile.category,
        '-',
        padStart(counter[profile.category], 2, '0')
      ].join('')
    }
  })
}

export default assignFixtureIds(fixtures(fixtureHelpers))
