import React, { Component } from 'react'
import { AutoComplete } from 'antd'
import * as R from 'ramda'

import fixtures from './fixtures'

const filterSortFixtureProps = R.pipe(
  R.sortBy(R.identity),
  R.without(['geolocation', 'category'])
)

const formatValue = a => {
  if (
    a !== null &&
    typeof a === 'object' &&
    (a.min !== undefined ||
      a.max !== undefined ||
      a.starts !== undefined ||
      a.ends !== undefined)
  ) {
    // It's range
    const from = a.min || a.starts
    const to = a.max || a.ends
    return from === to ? `${from}` : `${from}..${to}`
  }
  return JSON.stringify(a)
}

const fixtureLabel = fixture =>
  filterSortFixtureProps(R.keys(fixture.profile))
    .map(key => `${key}=${formatValue(fixture.profile[key])}`)
    .join(', ')

const Option = AutoComplete.Option

const flatOptions = fixtures.map(fixture => (
  <Option
    key={fixture.fixtureId}
    value={fixture.fixtureId}
    full={JSON.stringify(fixture.profile) + fixture.fixtureId}
  >
    {fixture.fixtureId}
    <div>
      <small>{fixtureLabel(fixture)}</small>
    </div>
  </Option>
))

class FixtureSelector extends Component {
  render() {
    return (
      <AutoComplete
        dataSource={flatOptions}
        filterOption={(inputValue, option) =>
          option.props.full.toUpperCase().indexOf(inputValue.toUpperCase()) !==
          -1}
        onSelect={this.props.onChange}
        allowClear={false}
        placeholder="Select a fixture"
        style={this.props.style}
        optionLabelProp="value"
      />
    )
  }
}

export default FixtureSelector
