import React from 'react'
import * as R from 'ramda'
import { Slider } from 'antd'
import { radiusStepsForProfile } from '@findy-sources/meta'

class RadiusSelector extends React.Component {
  onChange = ([min, max]) => {
    const steps = radiusStepsForProfile({ category: this.props.category })
    this.props.onChange({ min: steps[min], max: steps[max] })
  }

  render() {
    const steps = radiusStepsForProfile({ category: this.props.category })
    const marks = R.pipe(R.addIndex(R.map)((v, k) => [k, v]), R.fromPairs)(
      steps
    )

    return (
      <Slider
        range
        value={[
          R.findIndex(R.equals(this.props.radius.min), steps),
          R.findIndex(R.equals(this.props.radius.max), steps)
        ]}
        max={steps.length - 1}
        marks={marks}
        onChange={this.onChange}
      />
    )
  }
}

export default RadiusSelector
