export const state = { calls: [] }

export const reset = () => { state.calls = []}

export default async (requestObject) => {
  // normalize
  const obj = typeof requestObject === 'string' ? { uri: requestObject } : requestObject
  state.calls.push(obj)
  const url = '/proxy'
  const options = {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(obj)
  }
  const response = await (await fetch(url, options)).text()
  obj.response = response
  return response
}
