import React, { Component } from 'react'
import styled from 'styled-components'

const Value = styled.div`
  font-size: 18px;
  padding: 20px;
  margin: 10px;
  border: 1px dashed gray;
  border-radius: 20px;
`

class LocalTypes extends Component {
  render() {
    const { type, values } = this.props.filter
    return (
      <div>
        {type === 'list' &&
          values.map(value => <Value key={value}>{value}</Value>)}
        {type === 'compound' && <Value>Compound not yet supported</Value>}
      </div>
    )
  }
}

export default LocalTypes
