import React from 'react'
import { Collapse, Row, Col, Radio, Slider } from 'antd'
import Inspector from 'react-inspector'
import { Value, State } from 'react-powerplug'
import { pure } from 'recompose'

const Panel = Collapse.Panel

/**
 * Parses json text to an object. Returns undefined if text is not an object.
 */
const parseJSON = text => {
  try {
    return JSON.parse(text)
  } catch (e) {}
}

/**
 * Parses html text to a DOM tree. Returns undefined if text is probably not an html document.
 */
const parseHTML = text => {
  const parser = new DOMParser()
  const data = parser.parseFromString(text, 'text/html')
  if (data.body.childElementCount !== 0) {
    return data
  }
}

/**
 * Parses urlencoded text a DOM tree. Returns undefined if text is probably not urlencoded.
 */
const parseURLEncoded = text => {
  const params = Array.from(new URLSearchParams(text))
  if (params.length > 1) {
    return params
  }
}

const InspectorWithSlider = ({ data }) => (
  <State initial={{ expandLevel: 2 }}>
    {({ state, setState }) => (
      <React.Fragment>
        <Slider
          style={{ width: 150 }}
          min={1}
          max={15}
          defaultValue={state.expandLevel}
          onAfterChange={expandLevel =>
            setState({ expandLevel: undefined }, () =>
              setState({ expandLevel })
            )}
        />
        {state.expandLevel && (
          <Inspector expandLevel={state.expandLevel} data={data} />
        )}
      </React.Fragment>
    )}
  </State>
)

const UrlEncodedBody = ({ encoded }) => (
  <div>
    {encoded.map(([key, value]) => (
      <Row key={key}>
        <Col span={4}>
          <em>{key}:</em>
        </Col>
        <Col span={20}>{value}</Col>
      </Row>
    ))}
  </div>
)

const TextBody = ({ body }) => (
  <pre style={{ wordBreak: 'break-word', whiteSpace: 'pre-wrap' }}>{body}</pre>
)

const RequestBody = ({ body }) => {
  const json = parseJSON(body)
  const html = parseHTML(body)
  const encoded = parseURLEncoded(body)
  const data = json || html || encoded

  return (
    <Value initial="plain">
      {({ value, set }) => (
        <div>
          <Radio.Group
            value={value}
            onChange={e => set(e.target.value)}
            style={{ marginBottom: 16 }}
          >
            <Radio.Button value="plain">Plain</Radio.Button>
            {data && <Radio.Button value="inspector">Inspector</Radio.Button>}
            {encoded && (
              <Radio.Button value="encoded">URL Encoded</Radio.Button>
            )}
          </Radio.Group>

          {value === 'inspector' ? (
            <InspectorWithSlider data={data} />
          ) : value === 'encoded' ? (
            <UrlEncodedBody encoded={encoded} />
          ) : (
            <TextBody body={body} />
          )}
        </div>
      )}
    </Value>
  )
}

const ResponseBody = ({ body }) => {
  const json = parseJSON(body)
  const html = parseHTML(body)
  const data = json || html

  return (
    <Value initial="plain">
      {({ value, set }) => (
        <div>
          <Radio.Group
            value={value}
            onChange={e => set(e.target.value)}
            style={{ marginBottom: 16 }}
          >
            <Radio.Button value="plain">Plain</Radio.Button>
            {data && <Radio.Button value="inspector">Inspector</Radio.Button>}
          </Radio.Group>

          {value === 'inspector' ? (
            <InspectorWithSlider data={data} />
          ) : (
            <TextBody body={body} />
          )}
        </div>
      )}
    </Value>
  )
}

export default pure(({ calls = [] }) => (
  <React.Fragment>
    {calls.map((call, id) => (
      <Collapse key={id} defaultActiveKey={['0']} style={{ margin: 8 }}>
        <Panel header={`request #${id}`} key="0">
          <Row>
            <Col span={4}>
              <em>URL:</em>
            </Col>
            <Col span={20} style={{ wordBreak: 'break-word' }}>
              <a href={call.uri} target="_blank">
                {call.uri}
              </a>
            </Col>
          </Row>
          <Row>
            <Col span={4}>
              <em>Method:</em>
            </Col>
            <Col span={20}>{call.method}</Col>
          </Row>
          <Collapse>
            <Panel header="Headers">
              {Object.entries(call.headers).map(([key, value]) => (
                <Row key={key}>
                  <Col span={4}>
                    <em>{key}:</em>
                  </Col>
                  <Col span={20}>{value}</Col>
                </Row>
              ))}
            </Panel>
            <Panel header="Query string params">
              {Array.from(
                new URL(call.uri).searchParams
              ).map(([key, value]) => (
                <Row key={key}>
                  <Col span={4}>
                    <em>{key}:</em>
                  </Col>
                  <Col span={20}>{value}</Col>
                </Row>
              ))}
            </Panel>
            {call.body && (
              <Panel header="Request Body">
                <RequestBody body={call.body} />
              </Panel>
            )}
            {call.response && (
              <Panel header="Response Body">
                <ResponseBody body={call.response} />
              </Panel>
            )}
          </Collapse>
        </Panel>
      </Collapse>
    ))}
  </React.Fragment>
))
